import fetch from "../utils/fetch"


export function getAllProduct(data) {
    return fetch({
        url: "/projectSeries/selectAll",
        data,
        method: "post",
    })
}

export function createKey() {
    return fetch({
        url: "/projectSeries/newKey",
        method: "get",
    })
}

export function toggleProductStatus(data) {
    return fetch({
        url: "/projectSeries/onShelf",
        method: "put",
        data
    })
}
export function addProductSeries(data) {
    return fetch({
        url: "/projectSeries/addProductSeries",
        method: "put",
        data
    })
}

export function getProductDetail(data) {
    return fetch({
        url: "/projectSeries/getDetail/"+ data.id,
        method: "get",
    })
}

export function getProductShow() {
    return fetch({
        url: "/projectSeries/getProductShow",
        method: "get",
    })
}
