<template>
	<div class="produce-wrapper">
		<div class="header">
			<div class="title">产品列表</div>
			<div>
				<button class="primary-btn" @click="addProduct">新增产品</button>
				<button class="primary-btn" @click="toggleStatus(1)">上架</button>
				<button class="primary-btn" @click="toggleStatus(0)">下架</button>
			</div>
		</div>
		<div class="split-line"></div>
		<div class="main-content">
			<div class="search-area">
				<div class="input-area">
					<div class="input-item">
						<span>产品：</span>
						<el-input v-model="product_name" placeholder="请输入"></el-input>
					</div>
					<div class="input-item">
						<span>状态：</span>
						<el-select v-model="status" placeholder="请选择">
							<el-option
								v-for="item in options"
								:key="item.value"
								:label="item.label"
								:value="item.value"
							>
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="search-btn" @click="searchProduct">
					查询
				</div>
			</div>
			<div class="table-content">
				<el-table
					ref="multipleTable"
					:data="tableData"
					tooltip-effect="dark"
					style="width: 100%"
					:stripe="true"
					@row-click="getDetail"
					@selection-change="changeSelection"
				>
					<el-table-column type="selection" width="55" align="center">
					</el-table-column>
					<el-table-column label="序号" width="120" align="center">
						<template slot-scope="scope">{{
							(scope.$index + 1) * current_page
						}}</template>
					</el-table-column>
					<el-table-column prop="chName" label="产品" align="center">
					</el-table-column>
					<el-table-column prop="totalDownloads" label="下载次数/次" align="center">
					</el-table-column>
					<el-table-column prop="activateTimes" label="激活次数/次" align="center">
					</el-table-column>
					<el-table-column prop="status" label="状态" align="center">
						<template slot-scope="scope"
							><span
								:style="{
									color: scope.row.status == '1' ? '#425acb' : '#595959',
								}"
								>{{ scope.row.status == "0" ? "已下架" : "已上架" }}</span
							></template
						>
					</el-table-column>
					<el-table-column prop="updateTime" label="更新时间" align="center">
					</el-table-column>
				</el-table>
			</div>
			<div class="page-area">
				<el-pagination
					:current-page.sync="current_page"
					background
					layout="->,prev, pager, next"
					prev-text="上一页"
					next-text="下一页"
					:total="total"
					@current-change="searchProductByPage"
				>
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
import { getAllProduct, toggleProductStatus } from "../../api/product";
export default {
	data() {
		return {
			current_page: 1,
			count: 1,
			options: [
				{
					value: "",
					label: "全部",
				},
				{
					value: "1",
					label: "已上架",
				},
				{
					value: "0",
					label: "已下架",
				},
			],
			status: "",
			product_name: "",
			tableData: [],
			total: 1,
			selection: [],
		};
	},
	created() {},
	mounted() {
		// let token = sessionStorage.getItem("token");
		// if (!token) {
		// 	console.log('重新请求');
		// 	getUserToken()
		// 		.then((res) => {
		// 			sessionStorage.setItem("token", res.data.data.token);
		// 			verifyToken()
		// 				.then((result) => {
		// 					this.searchProduct();

		// 					this.$store.commit("setUserName", result.data.data.username);
		// 				})
		// 				.catch((e) => {
		// 				});
		// 		})
		// 		.catch((e) => {
		// 		});
		// } else {
		// 	this.searchProduct();
		// }
		this.searchProduct();
		this.$store.commit("setRouteName", {
			name: "产品管理",
			path: "/productManage",
		});
	},
	computed: {},
	methods: {
		searchProduct() {
			let data = {
				chName: this.product_name,
				page: 1,
				size: 10,
			};
			if (this.status !== "") {
				data.status = this.status;
			}
			getAllProduct(data).then((res) => {
				this.changeData(res);
			});
		},
		searchProductByPage(page) {
			let data = {
				chName: this.product_name,
				page,
				size: 10,
			};
			if (this.status !== "") {
				data.status = this.status;
			}
			getAllProduct(data).then((res) => {
				this.changeData(res);
			});
		},
		changeData(data) {
			this.tableData = data.data.data.rowList;
			this.current_page = data.data.data.currentPage;
			this.total = data.data.data.count;
			this.tableData.forEach((item) => {
				item.updateTime = item.updateTime.slice(0, 10);
			});
		},
		addProduct() {
			this.$router.push({
				path: "/addProduct",
			});
		},
		getDetail(row) {
			this.$router.push("/productDetail/" + row.id);
		},
		toggleStatus(status) {
			if (!this.selection.length) {
				this.$message({
					type: "warning",
					message: "您还未选择产品！",
				});
				return;
			}
			toggleProductStatus({
				type: status,
				productSeriesIds: this.selection,
			}).then((res) => {
				this.searchProductByPage(this.current_page);
				this.$message({
					type: "success",
					message: status ? "上架成功" : "下架成功",
				});
			});
		},
		changeSelection(selection) {
			this.selection = selection.map((item) => {
				return item.id;
			});
		},
	},
};
</script>

<style lang="scss" scoped>
.produce-wrapper {
	.header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		.title {
			font-size: 24px;
			color: #313552;
			font-weight: bold;
		}
		.primary-btn {
			width: 154px;
			height: 44px;
			line-height: 42px;
			font-size: 18px;
			color: #313552;
			border-radius: 22px;
			background-color: rgba(66, 90, 203, 0.1);
			border: 1px solid rgba(49, 53, 82, 0.4);
			font-size: 18px;
			font-family: "alifont";
			margin-left: 32px;
		}
		.primary-btn:active {
			background-color: #425acb;
			color: #fff;
			border-color: #425acb;
		}
	}
	.split-line {
		height: 1px;
		background-color: #d9d9d9;
		margin: 36px 0 32px 0;
	}
	.main-content {
		.search-area {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 28px;
			.input-area {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				.el-input,
				.el-select {
					width: 220px;
				}
				.input-item {
					margin-right: 88px;
					span {
						margin-right: 15px;
						font-size: 20px;
						color: #8c8c8c;
					}
				}
			}
			.search-btn {
				width: 120px;
				height: 44px;
				line-height: 44px;
				font-size: 18px;
				color: #fff;
				background-color: #425acb;
				border-radius: 5px;
				text-align: center;
				cursor: pointer;
			}
		}
		.table-content {
		}
		.page-area {
			margin-top: 33px;
		}
	}
}
</style>
